@import 'variables';
@import 'mixins';

/* ==========================================================================
   #Comments styles - start
   ========================================================================== */

.eltdf-comment-holder {
    @include eltdfRelativeHolderLayout();
    padding: 0;
    box-sizing: border-box;

    .eltdf-comment-holder-inner {
        padding: 41px 0 20px;
    }
    
    .eltdf-comments-title {
        margin-bottom: 28px;
    }

    .eltdf-comment {

        &.eltdf-no-avatar {

            .eltdf-comment-text {
                padding: 0;
            }
        }
    }

    .eltdf-comment-list {
        position: relative;
        padding: 0;
        margin: 0;
        list-style: none;
        z-index: 150;

        li {
            @include eltdfRelativeHolderLayout();
            margin: 0;
            padding: 23px 0 0;
        }

        > li:first-child {
            margin: 0;
            padding: 0;
            border: 0;
        }

        .children {
            padding: 0 0 0 126px;
        }
    }

    .eltdf-comment-image {
        position: relative;
        top: 10px;
        left: 0;
        width: 90px;
        height: 90px;
        display: block;
        float: left;

        img {
            display: block;
        }
    }

    .eltdf-comment-text {
        padding: 8px 0 0 124px;

        >a{
            position: absolute;
            right: 0;
            z-index: 10;
        }
        .comment-edit-link{
            right: 40px;

            @include phone-landscape{
                right: 33px;
            }
        }
        .comment-reply-link{
            color: transparent!important;

            &:hover{
                &:before{
                    color: $first-main-color;
                }
            }
            &:before{
                content: "\f2f8";
                font-family: Ionicons;
                font-size: 23px;
                color: $third-main-color;
                position: absolute;
                z-index: 0;
                transform: rotateY(180deg);
            }
        }



        .eltdf-comment-info {
            @include eltdfRelativeHolderLayout();
            margin: 6px 0 0;
        }

        .replay,
        .comment-reply-link,
        .comment-edit-link {
            float: right;
            margin-left: 10px;
            margin-top: 4px;
            font-size: 12px;
            color: $first-main-color;

            &:hover {
                color: $default-heading-color;
            }
        }

        .eltdf-comment-name {
            float: left;
            margin: 0;
            @include eltdfH5();
        }

        .eltdf-comment-date {
            float: left;
            margin: 0;
            font-size: 12px;
        }

        .eltdf-text-holder p {
            margin: 7px 0;
        }

        .comment-respond {
            margin: 20px 0;

            .comment-reply-title {
                margin: 0 0 10px;
                font-size: 14px;
            }

            .logged-in-as {
                margin: 0 0 8px;
            }
        }

        #cancel-comment-reply-link {
            margin-left: 10px;
            font-size: 12px;
            color: $first-main-color;

            &:hover {
                color: $default-heading-color;
            }
        }

        ul, ol {

            li {
                display: list-item;
            }
        }

        li {
            margin: 0 !important;
            padding: 0 !important;
            border: 0 !important;
        }
    }
}

.eltdf-comment-form {
    @include eltdfRelativeHolderLayout();
    margin: 72px 0 40px;
    clear: both;

    .comment-reply-title {
        margin: 0 0 41px;
    }

    .logged-in-as {
        margin: 0 0 8px;
    }

    form#commentform{
        input{
            margin: 25px 0 25px;
        }
    }
}

body{
    .eltdf-page-grid-line-one{
        position: absolute;
        width: 1px;
        border-left: 1px solid black;
        height: 100vh;
        left: 200px;
        top: 0;
        z-index: 1000;
    }
}

@include ipad-portrait {

    .eltdf-comment-holder {

        .eltdf-comment-list {

            .children {
                padding: 0 0 0 40px;
            }
        }
    }
}

@include phone-landscape {

    .eltdf-comment-holder {

        .eltdf-comment-list {

            .children {
                padding: 0;
            }
        }

        .eltdf-comment-text {

            .eltdf-comment-date,
            .eltdf-comment-name {
                float: none;
            }

            .replay,
            .comment-reply-link,
            .comment-edit-link {
                float: none;
                margin: 0 10px 0 0;

                @include phone-landscape{
                    margin: 0 4px 0 0;
                }
            }
        }
    }
}

@include phone-portrait {

    .eltdf-comment-holder {

        .eltdf-comment-image {
            float: none;
        }

        .eltdf-comment-text {
            padding: 20px 50px 0 0;
            min-height: inherit;
        }
    }
}

