        }
    }

    .eltdf-content-inner {
        @include eltdfTableLayout();
    }

    .eltdf-page-not-found {
        @include eltdfTableCellLayout();

        padding: 0 20%;
        text-align: center;

        .eltdf-404-title {
            margin: 0;
        }

        .eltdf-404-subtitle {
            margin: 35px 0 0;
        }

        .eltdf-404-text {
            margin: 10px 0 0;
        }

        .eltdf-btn {
            margin: 30px 0 0;

            &.eltdf-btn-light-style {
                color: #585858;
                background-color: #fff;
                border-color: #fff;

                &:hover {
                    color: #fff !important;
                    background-color: transparent !important;
                    border-color: #fff !important;
                }
            }
        }
    }

    @include ipad-landscape {
        .eltdf-content {
            height: 100%;
            min-height: 100vh;
        }

        &.admin-bar {
            .eltdf-content {
                height: 100%;
                min-height: calc(100vh - 32px);
            }
        }
    }

    @include ipad-portrait {
        .eltdf-page-not-found {
            padding: 70px 30px 20px;
        }
    }
}

