/* ==========================================================================
   #Back to Top styles - start
   ========================================================================== */
#eltdf-back-to-top {
    position: fixed;
    width: 41px;
    right: 25px;
    bottom: 25px;
    margin: 0;
    z-index: 10000;
    opacity: 0;
    visibility: hidden;

    &:hover {
            
        .qodef-svg-nav-arrow {

            polyline {
                &:nth-of-type(1) {
                stroke-dashoffset: 0;
                transition: .4s .4s linear;
              }
              &:nth-of-type(2) {
                stroke-dashoffset: 50;
                transition: .4s linear;
              }
            }
        }
    }

    &.off {
        opacity: 0;
        visibility: hidden;

        transition: opacity 0.15s ease, visibility .0s .15s;
    }

    &.on {
        opacity: 1;
        visibility: visible;
        transition: opacity 0.3s ease;
    }

    .eltdf-back-to-top-text{
        transform: rotateZ(-90deg);
        color: $default-text-color;
        width: 100px;
        height: 120px;
        font-size: 13px;
    }

    > span {
        position: relative;
        display: block;
        width: 45px;
        height: 45px;
        line-height: 40px;
        font-size: 15px;
        text-align: center;
        color: $default-text-color;
        background-color: #373737;
        overflow: hidden;
        transition: color 0.2s ease-in-out, background-color 0.2s ease-in-out, border-color 0.2s ease-in-out;

       .qodef-svg-nav-arrow {
            transform: rotateZ(-90deg);
            width: 16px;
            height: 16px;
            stroke: #fff;
            top: 6px;
            position: relative;
            stroke-width: 1.6px;
        }
    }
}

