.eltdf-page-grid{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;

    .eltdf-grid-line{
        width: 20%;
        display: block;
        float: left;
        position: relative;
        height: 100%;
        box-sizing: border-box;

        &:before{
            position: absolute;
            width: 1px;
            background-color: $third-background-color;
            content: "";
            height: 100%;
            left: 0;
            transform-origin: bottom left;
            -webkit-transition: -webkit-transform .75s cubic-bezier(.47,.94,.04,.98) .4s;
            -moz-transition: -moz-transform .75s cubic-bezier(.47,.94,.04,.98) .4s;
            transition: transform .75s cubic-bezier(.47,.94,.04,.98) .4s;
        }
    }
}

