.qodef-svg-nav-arrow {
    cursor: pointer;
    
    polyline {
      &:nth-of-type(1) {
        stroke-dasharray: 40;
        stroke-dashoffset: 40;
        transform: scaleY(-1);
        transform-origin: center;
        transition: .4s cubic-bezier(0.49, 0.48, 1, 1);
      }
      &:nth-of-type(2) {
        stroke-dasharray: 40;
        stroke-dashoffset: 0;
        transition: .4s .4s cubic-bezier(0.49, 0.64, 1, 1);
      }
    }
    
    &:hover {
      
      polyline {
          &:nth-of-type(1) {
          stroke-dashoffset: 0;
          transition: .4s .4s cubic-bezier(0.49, 0.64, 1, 1);
        }
        &:nth-of-type(2) {
          stroke-dashoffset: 40;
          transition: .4s cubic-bezier(0.49, 0.48, 1, 1);
        }
      }
    }
}

.eltdf-svg-close-cursor {
    opacity: 0;
    margin: -11px;
    pointer-events: none;
    position: fixed;
    z-index: 8000;

    &.eltdf-svg-close-cursor-light {
        svg {
            line {
                stroke: #fff;
            }
        }
    }

    svg {
        line {
            stroke-width: 1.2px;
        }
    }
}

.eltdf-custom-elements-holder-reveal {
    -webkit-clip-path: inset(0 100% 0 0);
    clip-path: inset(0 100% 0 0);
    transition: 1.2s cubic-bezier(0.63, 0.25, 0.25, 1);

    &.eltdf-appear {
		-webkit-clip-path: inset(0 0 0 0);
        clip-path: inset(0 0 0 0);
	}
}

